import { useState } from 'react';

export default function Page() {
  const [count, setCount] = useState(0);

  const handleChange = () => {
    setCount(count + 1);
  };

  return (
    <div>
      <h1>{count}</h1>
      <button onClick={handleChange}>ボタン</button>
    </div>
  );
}
